    <span class="token punctuation">[</span>short_url<span class="token punctuation">]</span> <span class="token operator">=></span> https<span class="token punctuation">:</span><span class="token comment">//w.url.cn/s/Aq7jWrd</span>
<span class="token punctuation">)</span>
